import './App.css';
import {Header} from "./components/Common/Header";
import {Notifications} from "./components/Notifications/Notifications";
import React, {createContext, useState} from "react";
import ReactGA from "react-ga4";
import {Context} from "react";
import {SDKProvider, useViewport} from "@tma.js/sdk-react";
import {AuthView} from "./components/WelcomePage/AuthView";

// Analytics
if (process.env.NODE_ENV === "production") {
    ReactGA.initialize("G-J76XC9PDBV");
}

export let NotificationsContext : Context<(text: string) => void>;


function App() {
    const [notifications, setNotifications] = useState([] as string[]);
    
    const addNotification = (text : string) => {
        setNotifications(notifications.concat([text]));
    }

    NotificationsContext = createContext(addNotification)
    
    return (
        <SDKProvider>
            <div className="mini-app-main-view">
                <Header/>
                <Notifications notificationList={notifications}/>
                <AuthView/>
            </div>
        </SDKProvider>
    );
}

export default App;
