import {TaskEntry} from './TaskEntry'
import React from "react";

import "./TaskList.css"
import {Task} from "../../communication/types";

export function TaskList({tasks} : {tasks: Task[]}) {
    // Kinda sorting
    const notClaimedTasks = tasks.filter(e => e.status !== "claimed")
    const claimedTasks = tasks.filter(e => e.status === "claimed")
    const sortedTasks = notClaimedTasks.concat(claimedTasks)
    
    return (
        <div className="friend-list">
            {sortedTasks.map((element, i) =>
                <React.Fragment key={i}>
                    <TaskEntry task={element}/>
                </React.Fragment>)
            }
        </div>
    );
}