import {FriendsView} from "../Friends/FriendsView";
import {MainView} from "../MainView/MainView";
import {NavigationFooter, NavigationFooterProps} from "./NavigationFooter";

import "./Navigation.css"
import {useState} from "react";
import {TasksView} from "../TasksView/TasksView";

export function Navigation(){
    const [selected, setSelected] = useState(0);
    
    const strideStyle = (relative : number) => {
        return {left: `calc(${relative - selected} * 100%)`}
    }
    
    const combinedScreens = <div className="navigation-stack">
        <div className="navigation-screen" style={strideStyle(0)}><MainView/></div>
        <div className="navigation-screen" style={strideStyle(1)}><FriendsView/></div>
        <div className="navigation-screen" style={strideStyle(2)}><TasksView/></div>
    </div>

    const navProps: NavigationFooterProps = {
        selected: selected,
        setSelected: setSelected
    }

    return <>
        {combinedScreens}
        <NavigationFooter props={navProps}/>
    </>
}