import '../Friends/FriendEntry.css'
import './TaskEntry.css'
import {formatBalanceNumber} from "../../communication/format";
import {patchTask} from "../../communication/communication";
import {useContext, useEffect} from "react";
import {ReloadTasksContext} from "./TasksView";
import {Task} from "../../communication/types";


const arrow = <div style={{width: "4rem"}}>
    <svg width="6" height="10" viewBox="0 0 6 10" fill="none" xmlns="http://www.w3.org/2000/svg">
        <path
            d="M5.70711 5.70709C5.89464 5.51955 6 5.2652 6 4.99998C6 4.73476 5.89464 4.48041 5.70711 4.29287L1.70711 0.292871C1.42111 0.00687307 0.990991 -0.078682 0.617316 0.0760984C0.243642 0.23088 -4.11083e-07 0.595516 -3.93403e-07 0.999978L-4.37127e-08 8.99997C-2.60331e-08 9.40443 0.243643 9.76907 0.617317 9.92385C0.990991 10.0786 1.42111 9.99308 1.70711 9.70708L5.70711 5.70709Z"
            fill="#5E5E5E"/>
    </svg>
</div>

const check = (color: string) => <div style={{width: "4rem"}}>
    <svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
        <path d="M18 8L9.75 16.4L6 12.5818" stroke={color} strokeWidth="3" strokeLinecap="round"
              strokeLinejoin="round"/>
    </svg>
</div>

const circle =  <div style={{width: "4rem"}}>
        <svg className="spinning" width="20" height="20" viewBox="0 0 20 20" fill="none" xmlns="http://www.w3.org/2000/svg">
            <path d="M10 18C14.4183 18 18 14.4183 18 10C18 5.58172 14.4183 2 10 2C5.58172 2 2 5.58172 2 10" stroke="white"
                  strokeWidth="3" strokeLinecap="round"/>
        </svg>
    </div>

export function TaskEntry({task}: { task: Task }) {
    const reloadTasks = useContext(ReloadTasksContext)
    
    useEffect(() => {
        if (task.status === "inprogress")
            setTimeout(reloadTasks, task.finish_ts - new Date().getTime() + 1000)
    }, [])
    
    const redirectMarkRefresh = () => {
        if (task.status !== "open") return;
        
        const patched = {
            status: "inprogress",
            finish_ts: new Date().getTime() + 1000 * 5
        }
        patchTask(task.task_id, JSON.stringify(patched))
            .then(_ => reloadTasks())
        
        setTimeout(reloadTasks, patched.finish_ts - new Date().getTime() + 1000)
    }

    const gray = "#5e5e5e"
    const blue = "#207bf9"

    // Case for open and default values
    let entryCssClass = "friend-entry clickable"
    let entryStatusIcon = arrow
    
    if (task.status === "claimed") {
        entryCssClass = "friend-entry task-entry-done";
        entryStatusIcon = check(gray)
    }
    if (task.status === "done") {
        entryCssClass = "friend-entry";
        entryStatusIcon = check(blue)
    }
    if (task.status === "inprogress") {
        entryCssClass = "friend-entry";
        entryStatusIcon = circle
    }
    
    
    return (
        <div className={entryCssClass} onClick={redirectMarkRefresh}>
            <img className="friend-picture" crossOrigin="" src={task.icon_url} alt="pic"/>
            <div className="friend-name-bonus-union">
                <div className="box-down">
                    <span className="task-name"> {task.name} </span>
                </div>
                <span className="friend-bonus bonus-gray"> {`+${formatBalanceNumber(task.bonus)} YEP`} </span>
            </div>
            <div className="box-right">
                <span className="task-status-icon"> {entryStatusIcon} </span>
            </div>
        </div>
    )
}