import React, {Context, createContext, useContext, useEffect, useState} from 'react';
import {TelegramName} from "./TelegramName";
import {BalanceCounter} from "./BalanceCounter";
import {ClaimButton} from "./ClaimButton"

import './MainView.css'

import {getUser, LOADED, LOADING} from '../../communication/communication'
import {NotificationsContext} from "../../App";
import {formatBalanceNumber} from "../../communication/format";
import {LoadingCenter} from "../Common/LoadingPlaceholder";
import {loading} from "../../communication/wrapped";
import {WaUser} from "../../communication/types";

export let AddBalanceContext: Context<(revenue: number) => void>;


const menuLoading = <div className="main-view">
    <div/>
    <div className="balance-display">
        <LoadingCenter className="l-rounded l-margin loading-telegram-name"/>
        <LoadingCenter className="l-rounded l-margin loading-balance-span"/>
    </div>
    <div/>
    <div className="claim-button-outer">
        <LoadingCenter className="l-rounded claim-button"/>
    </div>
</div>  


export function MainView() {
    const [oldBalance, setOldBalance] = useState(0)
    const [user, setUser] = useState(loading<WaUser>(null!))

    const addBalance = (revenue: number) => {
        if (Number.isNaN(revenue)) {
            console.error("revenue is invalid");
            return;
        }
        const userCopy = structuredClone(user);
        userCopy.data!.balance += revenue;
        setUser(userCopy);
    }

    AddBalanceContext = createContext(addBalance);

    const reloadBalanceWithOld = () => {
        getUser().then(r => {
            if (user.statusCode === LOADED) setOldBalance(r.data!.balance);
            setUser(r);
        });
    }

    useEffect(reloadBalanceWithOld, []);

    const addNotification = useContext(NotificationsContext)

    // On balance change set notifications, but not first time
    const updateUser = () => {
        if (user.statusCode !== LOADED) return;
        if (oldBalance === user.data!.balance) return;
        
        setOldBalance(user.data!.balance)
        if (oldBalance === 0) return;

        addNotification(`+ ${formatBalanceNumber(user.data!.balance - oldBalance)} YEP`)
    }

    useEffect(() => {
        updateUser()
    }, [updateUser, user]);

    // Render
    // ----------------------------------------

    if (user.statusCode === LOADING)
        return (menuLoading);

    if (user.statusCode === LOADED) {
        return (
            <div className="main-view">
                <div/>
                <div className="balance-display">
                    <TelegramName/>
                    <BalanceCounter balance={user.data!.balance}/>
                </div>
                <div/>
                <div className="claim-button-outer">
                    <ClaimButton/>
                </div>
            </div>
        );
    }

    // Any errors
    return (menuLoading);
}