import React from "react";
import './Header.css';
import {initMiniApp} from "@tma.js/sdk-react";

export function Header() {
    const [miniApp] = initMiniApp();
    const closeMiniApp = () => {miniApp.close()}
    return (
        <div className="mini-app-header">
            <div className="y-center">
                <button className="header-cancel" onClick={closeMiniApp}>Cancel</button>
            </div>
            <div className="header-center y-center">
                <span className="header-name">Esprito Protocol</span>
                <br/>
                <span className="header-bot-span">bot</span>
            </div>
            <div/>
        </div>
    )
}