import {FriendEntry} from './FriendEntry'
import {Friend} from "../../communication/types"
import React from "react";

import "./FriendsList.css"
import {LoadingCenter} from "../Common/LoadingPlaceholder";

const loadingEntry = (k : number) => <LoadingCenter className="friend-entry" key={k}/>

export const loadingList = (elemCount : number) => <div className="friend-list">
    {Array(elemCount).fill(0).map((_, index) => loadingEntry(index))}
</div>

export function FriendList({friends}: { friends: Friend[] }) {
    if (friends.length === 0) {
        return (
            <div className="friend-list">
                <div className="invite-friend-tip-outer">
                    <span className="invite-friend-tip-span"> Invite friends to earn more YEP </span>
                </div>
            </div>
        )
    }

    return (
        <div className="friend-list">
            {friends.map((element, i) =>
                <React.Fragment key={i}>
                    <FriendEntry friend={element}/>
                </React.Fragment>)
            }
        </div>
    );
}