import React from 'react';
import './NavigationFooter.css'


const home = (color: string) => <svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
    <path
        d="M13.8418 2.16563C12.7585 1.32304 11.2415 1.32305 10.1582 2.16564L3.15818 7.61008C2.42742 8.17845 2 9.05237 2 9.97814V20C2 21.6568 3.34315 23 5 23H19C20.6569 23 22 21.6568 22 20V9.97814C22 9.05236 21.5726 8.17845 20.8418 7.61008L13.8418 2.16563Z"
        fill={color}/>
    <path
        d="M9 14C9 12.8954 9.89543 12 11 12H13C14.1046 12 15 12.8954 15 14V19C15 19.5523 14.5523 20 14 20H10C9.44772 20 9 19.5523 9 19V14Z"
        fill={color === active ? "#5e5e5e" : "#000"}/>
</svg> 


const friends = (color: string) => <svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
    <path
        d="M1.66504 17.1485C1.95761 17.454 2.41078 17.5424 2.79671 17.3692C3.18265 17.196 3.41786 16.7987 3.3841 16.377C3.27105 14.9651 3.7538 13.5183 4.83251 12.4396L9.39327 7.87884L11.2218 9.7074C11.5078 9.9934 11.9379 10.079 12.3116 9.92417C12.6853 9.76939 12.9289 9.40475 12.9289 9.00029L12.9289 2.00024C12.9289 1.73503 12.8236 1.48067 12.636 1.29314C12.4485 1.1056 12.1941 1.00024 11.9289 1.00024H4.92892C4.52446 1.00024 4.15982 1.24389 4.00504 1.61756C3.85026 1.99123 3.93581 2.42135 4.22181 2.70735L6.01462 4.50017L1.75731 8.75758C-0.554593 11.0695 -0.585361 14.7985 1.66504 17.1485Z"
        fill={color}/>
    <path
        d="M22.2639 6.85177C21.9713 6.54625 21.5181 6.45787 21.1322 6.63107C20.7463 6.80426 20.5111 7.20156 20.5448 7.62322C20.6579 9.03512 20.1751 10.482 19.0964 11.5607L14.5357 16.1214L12.7071 14.2928C12.4211 14.0068 11.991 13.9213 11.6173 14.0761C11.2436 14.2309 11 14.5955 11 15L11 22C11 22.2652 11.1054 22.5196 11.2929 22.7071C11.4804 22.8946 11.7348 23 12 23H19C19.4045 23 19.7691 22.7564 19.9239 22.3827C20.0787 22.009 19.9931 21.5789 19.7071 21.2929L17.9143 19.5001L22.1716 15.2427C24.4835 12.9307 24.5143 9.20178 22.2639 6.85177Z"
        fill={color}/>
</svg>

const tasks = (color: string) => <svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
    <rect x="1" y="3" width="4" height="4" rx="2" fill={color}/>
    <rect x="1" y="10" width="4" height="4" rx="2" fill={color}/>
    <rect x="1" y="17" width="4" height="4" rx="2" fill={color}/>
    <rect x="8" y="3" width="15" height="4" rx="2" fill={color}/>
    <rect x="8" y="10" width="15" height="4" rx="2" fill={color}/>
    <rect x="8" y="17" width="15" height="4" rx="2" fill={color}/>
</svg>

const active = "white"
const inactive = "#5E5E5E"

export type NavigationFooterProps = {
    selected: number
    setSelected: (arg : number) => void
}

export function NavigationFooter({props} : {props : NavigationFooterProps}) {
    const setSelectedConst = (n: number) => {
        return () => props.setSelected(n)
    }
    
    return (
        <div className="mini-app-navigation">
            <div></div>
            <div className="navigation-buttons">
                <button onClick={setSelectedConst(0)} id="nav-home-button" className="nav-button">
                    {home(props.selected === 0 ? active : inactive)}
                </button>
                <button onClick={setSelectedConst(1)} id="nav-friends-button" className="nav-button">
                    {friends(props.selected === 1 ? active : inactive)}
                </button>
                <button onClick={setSelectedConst(2)} id="nav-mode-button" className="nav-button">
                    {tasks(props.selected === 2 ? active : inactive)}
                </button>
            </div>
            <div></div>
        </div>
    );
}