import {FETCH_ERROR, LOADED} from "./communication";

export type Wrapped<T> = {
    statusCode: number
    data: T | null
    errorMessage: string | null
}

export function loaded<T>(x : T) : Wrapped<T> {
    return {
        statusCode: LOADED,
        data: x,
        errorMessage: null
    }
}

export function loading<T>(x : T) : Wrapped<T> {
    return {
        statusCode: FETCH_ERROR,
        data: x,
        errorMessage: null
    }
}

export function wrapStatus<T>(retrieve : (r : Response) => Promise<T>, response: Promise<Response>): Promise<Wrapped<T>> {
    return response.then((response) => {
        return (retrieve(response)).then(r => {
            return {statusCode: response.status, data: r, errorMessage: null}
        });
    }).catch((error) => {
        return {statusCode: 1, data: null, errorMessage: error.toString()};
    });
}