import './FriendEntry.css'
import {formatBalanceNumber} from "../../communication/format";
import {Friend} from "../../communication/types"

export function FriendEntry({friend}: {friend: Friend})
{
    const friendBalanceString = friend.balance_diff > 0 ? `+${formatBalanceNumber(friend.balance_diff)} YEP` : "0 YEP"
    const friendBalanceColor = friend.balance_diff > 0 ? "bonus-blue" : "bonus-gray"
    
    return (
        <div className="friend-entry">
            <img className="friend-picture" crossOrigin="" src={friend.photo_url} alt="pic"/>
            <div className="friend-name-bonus-union">
                <div className="box-down">
                    <span className="friend-nickname"> {friend.name} </span>
                </div>
                <span className={`friend-bonus ${friendBalanceColor}`}> {friendBalanceString} </span>
            </div>
            <div className="box-center">
                <span className="friend-balance y-center"> {`${formatBalanceNumber(friend.balance)} YEP`} </span>
            </div>
        </div>
    )
}