// Data for connection
import {InitData, useInitData} from "@tma.js/sdk-react";
import axios, {AxiosError, AxiosResponse} from "axios";
import {Wrapped, wrapStatus} from "./wrapped";
import {Friend, Task, WaUser} from "./types";
import createAuthRefreshInterceptor from "axios-auth-refresh";


let serverAddress = "http://127.0.0.1:8080";
let token = ""
let userId = ""


serverAddress = `https://api.esprito.com`



export const LOADING = 0;
export const FETCH_ERROR = 1;
export const LOADED = 200;
export const NOT_FOUND = 200;
export const UNAUTHORIZED = 401

// Setup token refresh
const refreshAuthLogic = (failedRequest : AxiosError) => {
    const hashEncoded = window.location.hash.slice(1)
    const hash = new URLSearchParams(hashEncoded)
    const initDataString = hash.get("tgWebAppData")
    if (failedRequest.status !== UNAUTHORIZED) return Promise.resolve();
    
    return postAuth(JSON.stringify({init_data: initDataString})).then((tokenRefreshResponse) => {
        setToken(tokenRefreshResponse.data.token_id);
        failedRequest.response!.config.headers['Authorization'] = 'Bearer ' + tokenRefreshResponse.data.token_id;
        return Promise.resolve();
    });
}

// Instantiate the interceptor
createAuthRefreshInterceptor(axios, refreshAuthLogic);


const retrieveJson = (r : Response) => r.json()


export function postAuth(payload : string){
    const init = {
        method: 'POST',
        headers: {"Content-Type": "application/json"},
        body: payload
    }
    return wrapStatus(retrieveJson, fetch(`${serverAddress}/api/auth`, init));
}


export function postUsers(initData : InitData) : Promise<Wrapped<WaUser>>{
    const init = {
        method: 'POST',
        headers: {"Content-Type": "application/json", "Authorization": `Bearer ${token}`},
        body: JSON.stringify(initData)
    }
    return wrapStatus(retrieveJson, fetch(`${serverAddress}/api/users`, init));
}

function fetchSigned<T>(url : string, method : string, body : string | null = null) : Promise<Wrapped<T>> {
    const headers : any = {"Content-Type": "application/json"};
    headers["Authorization"] = `Bearer ${token}`;
    const req = axios(url, {
        method: method,
        headers: headers,
        data: body
    });
    
    return req.then(res => {
        return {statusCode: res.status, data: res.data, errorMessage: null}
    }).catch((err : AxiosError) => {
        return {statusCode: err.status === undefined ? FETCH_ERROR : err.status, data: null, errorMessage: err.message};
    });
}

export function postClaim() : Promise<Wrapped<number>>{
    return fetchSigned(`${serverAddress}/api/users/${userId}/claim`, "POST");
}

export function patchPress(data : string) : Promise<Wrapped<any>> {
    return fetchSigned(`${serverAddress}/api/users/${userId}/press`, "PATCH", data);
}

export function getTasks() : Promise<Wrapped<Task[]>> {
    return fetchSigned(`${serverAddress}/api/users/${userId}/tasks`, "GET");
}

export function patchTask(taskId: number, body: string) : Promise<Wrapped<any>> {
    return fetchSigned(
        `${serverAddress}/api/users/${userId}/tasks/${taskId}`, 
        "PATCH",
        body);
}

export function taskClaim() : Promise<Wrapped<number>> {
    return fetchSigned(`${serverAddress}/api/users/${userId}/taskclaim`, "POST");
}

export function getUser() : Promise<Wrapped<WaUser>> {
    return fetchSigned(`${serverAddress}/api/users/${userId}`, "GET");
}

export function setToken(j : string) {
    token = j;
}

export function setUserId(id : string) {
    userId = id;
}

export function getFriends() : Promise<Wrapped<Friend[]>> {
    return fetchSigned(`${serverAddress}/api/users/${userId}/referrals`, "GET");
}

export function postClaimFriends() : Promise<Wrapped<number>>  {
    return fetchSigned(`${serverAddress}/api/users/${userId}/referalclaim`, "POST");
}