import "./RegisterView.css"
import {ProgressBarButton, ProgressBarButtonProps} from "../Common/ProgressBarButton";
import {postUsers} from "../../communication/communication";
import {useState} from "react";
import {InitData} from "@tma.js/sdk-react";
import {WaUser} from "../../communication/types";

export type registerProps = {
    register: (waUser : WaUser) => void,
    initData: InitData | null
    user: string
}

export function RegisterView({props} : {props : registerProps}) {
    const [canRegister, setCanRegister] = useState(true);
     
    const register = () => {
        if (props.initData === null) {
            setCanRegister(false);
            return;
        }
        
        postUsers(props.initData).then(r => {
            if (r.statusCode === 201 || r.statusCode === 200) props.register(r.data!);
            else setCanRegister(false);
        });
    }
    
    const buttonProps: ProgressBarButtonProps = {
        text: canRegister ? "Create account" : "Error occurred",
        progress: canRegister ? 1 : 0,
        onSuccessClick: register,
        onFailClick: () => {},
        backgroundColor: "#207bf9",
        textColor: canRegister ? "#ffffff" : "#808080",
    }
    
    // OK
    return (
        <>
            <div className="auth-view">
                <div></div>
                <div className="auth-data">
                    <span className="balance-span">{`YEP, ${props.user}!`}</span>
                    <br/>
                    <span className="auth-text"> We provide additional security layer for TON users in order to increase trust and support them in TON mass adoption process. </span>
                </div>
                <div></div>
                <div className="register-button">
                    <ProgressBarButton props={buttonProps}></ProgressBarButton>
                </div>
            </div>
            <div className="bg-black"/>
        </>
    )
}