import React, {Context, createContext, useEffect, useState} from "react";
import {getTasks, LOADED, LOADING} from "../../communication/communication";
import {TaskList} from "./TaskList";
import {ClaimTasksButton} from "./ClaimTasksButton";
import {loadingList} from "../Friends/FriendsList";
import {Loading, LoadingCenter} from "../Common/LoadingPlaceholder";
import {Task} from "../../communication/types";
import {loading, Wrapped} from "../../communication/wrapped";

const emptyTasks : Wrapped<Task[]> = loading([])

export let ReloadTasksContext: Context<() => void>


const tasksLoading = <div className="friends-view">
    <Loading className="loading-friends-board-header"/>
    {loadingList(6)}
    <LoadingCenter className="l-rounded claim-button"/>
</div>

export function TasksView() {
    const [tasks, setTasks] = useState(emptyTasks)

    useEffect(() => {
        getTasks().then(r => setTasks(r))
    }, []);

    ReloadTasksContext = createContext(() => {
        getTasks().then(r => setTasks(r))})

    if (tasks.statusCode === LOADING) return tasksLoading
    if (tasks.statusCode !== LOADED || tasks.data === null) return tasksLoading
    
    const amountToClaim = tasks.data
        .filter(e => e.status === "done")
        .reduce((a, c) => a + c.bonus, 0);
    
    return(
        <div className="friends-view">
            <span className="friends-board-header"> Tasks </span>
            <TaskList tasks={tasks.data}/>
            <ClaimTasksButton amount={amountToClaim}/>
        </div>
    );
}