import './InviteFriendButton.css'
import {initUtils, useInitData} from "@tma.js/sdk-react";

export function InviteFriendButton () {
    const utils = initUtils()
    const initData = useInitData()
    
    const shareRefLink = () => {
        if (initData?.user === undefined) return;
        const id = initData.user.id
        const encoded = id.toString(36);
        const url = `t.me/espritobot/app?startapp=${encoded}`
        const data = `https://t.me/share/url?url=${url}`
        utils.openTelegramLink(data)
    }
    
    return (
        <button onClick={shareRefLink} className="invite-friend-button">
            <span className="invite-friend-button-text"> Invite a friend </span>
        </button>
    )
}