import React from 'react';
import './TelegramName.css'
import {useInitData} from "@tma.js/sdk-react";

export function TelegramName() {
    const initData = useInitData();
    const user = initData && initData.user ? initData.user.username : "failed_to_load";
    const telegramNameDisplay = "@" + user;
    return <span className="telegram-name">{telegramNameDisplay}</span>
}