import {CloudStorage, useCloudStorage, useInitData, useViewport} from "@tma.js/sdk-react";
import {Navigation} from "../Navigation/Navigation";

import "./AuthView.css"
import {registerProps, RegisterView} from "./RegisterView";
import {useEffect, useState} from "react";
import {FETCH_ERROR, getUser, LOADED, NOT_FOUND, postAuth, setToken, setUserId} from "../../communication/communication";
import {LoadingCircle} from "../Common/LoadingPlaceholder";
import {loaded, Wrapped} from "../../communication/wrapped";
import {JwtResponse, WaUser} from "../../communication/types";

enum AuthStateType {
    Loading,
    Error,
    LoggedIn,
    NotRegistered,
}

type AuthStateData = {
    authState : AuthStateType,
    message: string
}

// Also used to refresh JWT in communication.ts
export async function requestUserJwt(initDataUrlEncoded: string): Promise<Wrapped<string>> {
    // Undefined to null
    const u2n = (v : any) => v !== undefined ? null : v;
    const retrieveTokenId = (r : Wrapped<JwtResponse>) : Wrapped<string> => {
        return {statusCode: r.statusCode, data: u2n(r.data?.token_id), errorMessage: r.errorMessage};
    }
    const payload = {init_data: initDataUrlEncoded};
    return postAuth(JSON.stringify(payload)).then(retrieveTokenId)
}

async function getUserJwt(cloud: CloudStorage, initDataUrlEncoded: string): Promise<Wrapped<string>> {
    try {
        const r = await cloud.get("auth");
        if (r === "") return requestUserJwt(initDataUrlEncoded)
        return loaded<string>(r)
    } catch (e) {
        return requestUserJwt(initDataUrlEncoded)
    }
}

function showErrorTsx(error: JSX.Element) {
    return <>
        <div className="xy-center">
        {error}
        </div><div/><div/>
        <div className="bg-black"/>
    </>
}

function showError(error : string){
    const elem = <span className="font-white x-center"> {error} </span>
    return showErrorTsx(elem)
}

function showLoading(){
    const elem = <>
        <span className="font-white x-center"> Loading </span>
        <LoadingCircle className="loading-circle-big"/>
    </>
    return showErrorTsx(elem)
}

export function AuthView() {
    // TODO: Relocate to another place
    const viewport = useViewport()
    viewport?.expand()
    
    const initData = useInitData()
    const cloud = useCloudStorage()
    const initialState = {authState: AuthStateType.Loading, message: "Loading"}
    const [authState, setAuthState] = useState<AuthStateData>(initialState)
    const hashEncoded = window.location.hash.slice(1)
    const hash = new URLSearchParams(hashEncoded)
    const initDataString = hash.get("tgWebAppData")

    let username = initData?.user?.username
    if (username === undefined) username = initData?.user?.firstName
    if (username === undefined) username = "user"
    
    useEffect(() => {
        if (initData?.user === undefined) {
            setAuthState({authState: AuthStateType.Error, message: "User field of telegram init data was not provided. Launch an app in telegram through @espritobot"});
            return;
        }
        if (initDataString === null) {
            setAuthState({authState: AuthStateType.Error, message: "Telegram init data was not provided. Launch an app in telegram through @espritobot"});
            return;
        }
        
        getUserJwt(cloud, initDataString).then(r => {
            setUserId(initData.user!.id.toString())
            if (r.statusCode === FETCH_ERROR) throw new Error("Unable to connect to server!")
            if (r.statusCode === LOADED) {
                getUser().then(r => {                    
                    // Check for user existance to show register view
                    if (r.statusCode === LOADED) setAuthState({authState: AuthStateType.LoggedIn, message: "Success"});
                    else setAuthState({authState: AuthStateType.NotRegistered, message: `Error ${r.statusCode}`})
                });
                cloud.set("auth", r.data!)
                setToken(JSON.parse(r.data!).token_id);
            }
            else if (r.statusCode === 404) setAuthState({authState: AuthStateType.NotRegistered, message: "Not registered"})
            else if (r.statusCode === 409) setAuthState({authState: AuthStateType.Error, message: "Conflict"})
            else if (r.statusCode === 401) setAuthState({authState: AuthStateType.Error, message: "Illegal auth"})
            else setAuthState({authState: AuthStateType.Error, message: `Error ${r.statusCode}`})
        }).catch(e => {
            setAuthState({authState: AuthStateType.Error, message: e.toString()})
        });
    }, []);

    if (initData === undefined)
        return showError("Open the app in telegram")

    const props : registerProps = {
        register: (waUser : WaUser) => {
            cloud.set("user_id", waUser.toString())
                .then(_ => setAuthState({authState: AuthStateType.LoggedIn, message: `Logged in`}));
        },
        initData: initData,
        user: username
    }
    
    if (authState.authState === AuthStateType.Loading) return showLoading();
    if (authState.authState === AuthStateType.NotRegistered) {
        return (<RegisterView props={props}/>);
    }
    if (authState.authState === AuthStateType.LoggedIn) return (<Navigation/>);
    
    return showError(authState.message)
}