import {useContext, useEffect, useState} from 'react'
import {ProgressBarButton, ProgressBarButtonProps} from "../Common/ProgressBarButton";
import {getUser, LOADED, patchPress, postClaim} from "../../communication/communication"

import './ClaimButton.css'
import {numberWithCommas, renderTime} from "../../communication/format";
import {AddBalanceContext} from "./MainView";
import {useCloudStorage} from "@tma.js/sdk-react";
import {Wrapped} from "../../communication/wrapped";
import {WaUser} from "../../communication/types";

// TODO: Change to proper value
const requiredTime = 1000 * 10; // 200 Sec

// TODO: Change to proper value
const singlePressRevenue = 1020;

enum ClaimButtonState {
    Farming, // Display usual progressbar button
    StartFarming, // Display white claim message
    Loading, // Display loading circle
}

const IS_FARMING = "isFarming"

export function ClaimButton() {
    const [state, setState] = useState(ClaimButtonState.Loading)
    const [currentTime, setCurrentTime] = useState(new Date().getTime())
    const [lastPressed, setLastPressed] = useState(0)

    const updateTime = () => {
        setCurrentTime(new Date().getTime());
    }

    const cloud = useCloudStorage()

    const addBalance = useContext(AddBalanceContext)

    const updateStatus = () => {
        getUser().then((r : Wrapped<WaUser>) => {
            if (r.statusCode !== LOADED) return;
            
            cloud.get(IS_FARMING)
                .then(isFarming => {
                    const next_claim_ts = r.data!.next_claim_ts !== null ? r.data!.next_claim_ts : 0
                    setState(isFarming === "true" ? ClaimButtonState.Farming : ClaimButtonState.StartFarming)
                    setLastPressed(next_claim_ts)
                })
                .catch(_ => cloud.set(IS_FARMING, "false"));
        });
    }

    // Setup updater
    useEffect(() => {
        updateStatus()
        setInterval(updateTime, 10);
    }, []);

    const progress = Math.min((currentTime - lastPressed) / requiredTime, 1);
    const timeDiff = lastPressed + requiredTime - currentTime;
    const text = numberWithCommas(singlePressRevenue * progress) + " YEP";

    // Update 
    const clickClaim = () => {
        postClaim().then(amount => {
            if (amount.statusCode !== LOADED || amount.data! <= 0) return;
            
            cloud.set(IS_FARMING, "false").then(_ => {
                setState(ClaimButtonState.StartFarming)
                addBalance(amount.data!)
            });
        });
    }

    const clickStart = () => {
        const patched = {
            next_claim_ts: new Date().getTime()
        }
        patchPress(JSON.stringify(patched)).then(r => {
            if (r.statusCode !== LOADED) return;
            setLastPressed(patched.next_claim_ts)
            cloud.set(IS_FARMING, "true").then(_ => setState(ClaimButtonState.Farming))
        });
    }

    const doNothing = () => {}


    // Render
    // ------------------------------------
    const props: ProgressBarButtonProps = {
        text: text,
        progress: progress,
        onSuccessClick: clickClaim,
        onFailClick: doNothing,
        backgroundColor: "#207bf9",
        textColor: "#ffffff",
    }

    const whiteClaimProps: ProgressBarButtonProps = {
        text: "Start farming",
        progress: 1,
        onSuccessClick: clickStart,
        onFailClick: doNothing,
        backgroundColor: "#ffffff",
        textColor: "#000000",
    }

    const loadingButtonProps: ProgressBarButtonProps = {
        text: "Loading",
        progress: 0,
        onSuccessClick: doNothing,
        onFailClick: doNothing,
        backgroundColor: "#5e5e5e",
        textColor: "#ffffff",
    }

    const timeDisplayContent = progress < 1 ? `Claim in ${renderTime(timeDiff)}` : "";
    const timeDisplay = <span className="button-subscript"> {timeDisplayContent} </span>;

    if (state === ClaimButtonState.Farming) {
        if (progress >= 1) props.text = "Claim " + props.text;
        return (<>
            <div className="claim-button"><ProgressBarButton props={props}/></div>
            {timeDisplay}
        </>);
    }

    if (state === ClaimButtonState.Loading)
        return <><div className="claim-button"><ProgressBarButton props={loadingButtonProps}/></div>{timeDisplay}</>

    return <><div className="claim-button"><ProgressBarButton props={whiteClaimProps}/></div>{timeDisplay}</>
}