import {Friend} from '../../communication/types'
import {FriendList, loadingList} from "./FriendsList";
import {useEffect, useState} from "react";
import {getFriends, LOADED, LOADING} from "../../communication/communication";
import {InviteFriendButton} from "./InviteFriendButton"
import {ClaimFriendsButton} from "./ClaimFriendsButton"

import './FriendsView.css'
import {Loading, LoadingCenter} from "../Common/LoadingPlaceholder";
import {loading, Wrapped} from "../../communication/wrapped";

const emptyFriends: Wrapped<Friend[]> = loading([])

const friendsLoading = <div className="friends-view">
    <Loading className="loading-friends-board-header"/>
    {loadingList(4)}
    <div>
        <div className="friends-view-buttons">
            <LoadingCenter className="l-rounded loading-invite-friend-button"/>
            <LoadingCenter className="l-rounded loading-friends-claim-button"/>
        </div>
        <LoadingCenter className="l-rounded l-margin loading-button-subscript"/>
    </div>
</div>

export function FriendsView() {
    const [friends, setFriends] = useState(emptyFriends)

    const fetchFriends = () => {
        getFriends().then(r => setFriends(r))
    }

    useEffect(() => {
        fetchFriends()
    }, []);

    if (friends.statusCode === LOADING) return friendsLoading
    if (friends.statusCode !== LOADED) return friendsLoading

    const bonus = friends.data!.reduce((a, c) => a + c.balance_diff, 1);
    
    
    return (
        <div className="friends-view">
            <span className="friends-board-header"> Friends Board </span>
            <FriendList friends={friends.data!}/>
            <div>
                <div className="friends-view-buttons">
                    <InviteFriendButton/>
                    <ClaimFriendsButton bonus={bonus} fetchFriends={fetchFriends}/>
                </div>
                <span className="button-subscript"> Get 10% from your friends farming every 24h </span>
            </div>
        </div>
    );
}