import {ProgressBarButton, ProgressBarButtonProps} from "../Common/ProgressBarButton";
import {formatBalanceNumber} from "../../communication/format";

import "./ClaimTasksButton.css"
import {useContext} from "react";
import {AddBalanceContext} from "../MainView/MainView";
import {taskClaim} from "../../communication/communication";
import {ReloadTasksContext} from "./TasksView";

const doNothing = () => {}

export function ClaimTasksButton({amount} : {amount: number}) {
    const addBalance = useContext(AddBalanceContext)
    const reloadTasks = useContext(ReloadTasksContext)

    const claimTasks = () => {
        taskClaim()
            .then(r => addBalance(+r.data!))
            .then(_ => reloadTasks())
    }
    
    const nothing = "Nothing to claim"
    const claimText = `Claim ${formatBalanceNumber(amount)} YEP`
    
    const props: ProgressBarButtonProps = {
        text: amount > 0 ? claimText : nothing,
        progress: amount > 0 ? 1 : 0,
        onSuccessClick: claimTasks,
        onFailClick: doNothing,
        backgroundColor: "#207bf9",
        textColor: "#ffffff",
    }
    
    return (<div className="claim-button">
        <ProgressBarButton props={props}/>
    </div>)
}