import './LoadingPlaceholder.css'

export function Loading({className} : {className : string}) {
    return <div className={`l-rounded loading-placeholder ${className}`}/>
}

export function LoadingCenter({className} : {className : string}) {
    return <div className="loading-center">
        <Loading className={className}/>
    </div>
}


const circleBig =  <div style={{width: "2rem"}}>
    <svg className="spinning" width="20" height="20" viewBox="0 0 20 20" fill="none" xmlns="http://www.w3.org/2000/svg">
        <path d="M10 18C14.4183 18 18 14.4183 18 10C18 5.58172 14.4183 2 10 2C5.58172 2 2 5.58172 2 10" stroke="white"
              strokeWidth="3" strokeLinecap="round"/>
    </svg>
</div>

export function LoadingCircle({className} : {className : string}) {
    return <div className={className}> {circleBig} </div>;
}